import React, { Component } from 'react';
import stagePic from './resources/stage.jpg';
import scotiaGroup from './resources/scotiaGroup.jpg';
import lakeLouise from './resources/lakeLouise.png'
import Carousel from "react-bootstrap/Carousel";

class Landing extends Component {
    constructor(props){
        super(props);
        this.handleSelect = this.handleSelect.bind(this);
        this.state = {
            displayNav: false,
            slideIndex: 0
        }
    }

    handleSelect(selectedIndex){
        this.setState({slideIndex: selectedIndex});
    }

    render() {
        return (
            <Carousel interval={3000} controls={false} activeIndex={this.state.slideIndex} onSelect={this.handleSelect} pause={false} style={{ display: "flex", width: "100%", height: "100%" }}>
                <Carousel.Item style={{ width: "100%", height: "100%" }}>
                    <div style={{ width: "100%", height: "100%", display: "flex", overflow: "hidden" }}>
                        <img
                            style={{ width: "100%", objectFit: "cover" }}
                            src={stagePic}
                            alt="First slide"
                        />
                        <Carousel.Caption>
                            <h3>Scotiabank End of Summer Showcase</h3>
                            <p>Presented my teams project as part of the Scotiabank Future Leaders competition</p>
                        </Carousel.Caption>
                    </div>
                </Carousel.Item>
                <Carousel.Item style={{ width: "100%", height: "100%" }}>
                    <div style={{ width: "100%", height: "100%", display: "flex", overflow: "hidden" }}>
                        <img
                            style={{width: "100%", objectFit: "cover" }}
                            src={scotiaGroup}
                            alt="Second slide"
                        />
                        <Carousel.Caption>
                            <h3>Future Leaders Team</h3>
                            <p>My team from the Future Leaders competition</p>
                        </Carousel.Caption>
                    </div>
                </Carousel.Item>
                <Carousel.Item style={{ width: "100%", height: "100%" }}>
                    <div style={{ width: "100%", height: "100%", display: "flex", overflow: "hidden" }}>
                        <img
                            style={{ width: "100%", objectFit: "cover" }}
                            src={lakeLouise}
                            alt="First slide"
                        />
                        <Carousel.Caption>
                            <h3>The Beehive</h3>
                            <p>Summited this mountain of 2,270 meters with a group of friends. Lake Louise seen below</p>
                        </Carousel.Caption>
                    </div>
                </Carousel.Item>
            </Carousel>
        )
    }
}

export default Landing;